/* Colors
-------------------------- */
/* icon
-------------------------- */
.field-query {
  width: 100%;
}
.field-query .select-box {
  background-color: white;
  width: 230px;
  position: fixed;
  list-style: none;
  margin: 0;
  border: 1px solid #EFF2F7;
  -webkit-box-shadow: 0 10px 15px 1px #eee;
          box-shadow: 0 10px 15px 1px #eee;
  z-index: 67;
}
.field-query .select-box li {
  cursor: pointer;
  border-bottom: 1px solid #EFF2F7;
  height: 35px;
  line-height: 35px;
  padding: 0 8px;
}
.field-query .select-box li:hover {
  background-color: #F9FAFC;
}



