/* icon
-------------------------- */
.field-select {
  width: 100%;
  display: block;
}
.field-select .select-box {
  background-color: white;
  position: absolute;
  list-style: none;
  margin: 0;
  border: 1px solid #EFF2F7;
  -webkit-box-shadow: 0 10px 15px 1px #eee;
          box-shadow: 0 10px 15px 1px #eee;
  z-index: 1000;
  max-height: 210px;
  width: 160px;
  overflow: hidden;
}
.field-select .select-box ul {
  min-width: 180px;
  max-height: 210px;
  overflow-y: auto;
}
.field-select .select-box li {
  cursor: pointer;
  border-bottom: 1px solid #EFF2F7;
  height: 35px;
  line-height: 35px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 8px;
}
.field-select .select-box li:hover {
  background-color: #F9FAFC;
}



